@import 'node_modules/@glidejs/glide/src/assets/sass/glide.core';
@import 'node_modules/@glidejs/glide/src/assets/sass/glide.theme';

.thumb {
  position: relative;
}

.slider-wrapper {
  background-color: rgb(247, 247, 247);
  padding: 20px 0;
  font-size: 13px;

  @media screen and (max-width: 1023px) {
    display: none;
  }
}

.glide__arrow {
  background-color: #ff6b08;
}

.slider-card {
  margin-right: 8px;
}

.slider-title {
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;

  color: black;
  font-family: 27px;
  font-size: 14px;
  font-weight: 700;
  line-height: 2;
  text-transform: uppercase;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.3);
}

.slider-image {
  border-radius: 5px;
}

.rating-slider {
  position: absolute;
  bottom: 0;
  right: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 38px;
  height: 18px;
  font-family: inherit;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.1;
  color: white;
  background-color: #ff6b08;
  border-radius: 5px;
}
